.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  font-family: "Roboto";
  margin-bottom: 48px;
}

.tooltip {
  background: #ff6f00 !important;
  font-family: "Ravie" !important;
  border: 1px solid #755713;
  opacity: 1 !important;
}

.card img {
  width: 184px;
  height: 271px;
  box-shadow: -12px -7px 13px rgba(0, 0, 0, 0.16);
  -webkit-box-shadow: -12px -7px 13px rgba(0, 0, 0, 0.16);
  -moz-box-shadow: -12px -7px 13px rgba(0, 0, 0, 0.16);
  margin-bottom: -10px;
  border: 5px solid #755713;
  border-radius: 10px;
  z-index: 1;
}

.card-right {
  width: 226px;
  min-height: 264px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  background-color: #e69f00;
  border: 1px solid #707070;
  box-shadow: -5px 3px 6px rgba(0, 0, 0, 0.16);
  position: relative;
}

.card-right h1 {
  font-size: 18px;
  margin-top: 12px;
  margin-left: 12px;
  text-align: left;
  display: inline;
  position: absolute;
  left: 0;
  max-width: 160px;
}
.card-right svg {
  fill: white;
  display: inline-block;
  width: 31px;
  height: 31px;
  position: absolute;
  right: 0;
  margin-top: 19px;
  margin-right: 13px;
}

.card-rigth-top {
  min-height: 60px;
}

.card-right a {
  cursor: default;
}
.card-rigth-top h1 {
  font-weight: bold;
}

.author,
.publisher {
  font-size: 16px;
}
hr {
  width: 80%;
